@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
* {
	padding: 0;
	margin: 0;
	border: 0;
}

*,
*:before,
*:after {
	box-sizing: border-box;
}

:focus,
:active {
	outline: none;
}

a,
a:visited,
a:hover {
	text-decoration: none;
}

a:focus,
a:active {
	outline: none;
}

nav,
footer,
header,
aside {
	display: block;
}

html,
body {
	height: 100%;
	width: 100%;
	line-height: 1;
	font-size: 14px;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
	font-family: "Nunito", sans-serif;
	scroll-behavior: smooth;
}

input,
button,
textarea {
	font-family: inherit;
}

input::-ms-clear {
	display: none;
}

button {
	cursor: pointer;
}

button::-moz-focus-inner {
	padding: 0;
	border: 0;
}

ul li {
	list-style: none;
}

img {
	vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-size: inherit;
	font-weight: 400;
}

/*---------------------------*/

body{
	background-color: #f5debd;
}

.container {
	text-align: center;
	max-width: 400px;
	margin: 0 auto;
	padding: 30px 10px;
}

.todo-header h1 {
	margin-bottom:15px;
	font-size: 37px; 
	font-weight: bold;
}

.todo-header h2 {
	margin-bottom:25px;
	font-size: 20px;
}

.todo-input {
	padding: 14px 14px 14px 14px;
	border-radius: 10px 0 0 10px;
	border: 2px solid #c20505;
	max-width: 200px;
	font-size: 17px;
	margin-bottom:30px;
}

.todo-button {
	padding: 16px 25px 16px 25px;
	border-radius: 0 10px 10px 0;
	cursor: pointer;
	background-color: #c20505;
	color: #fff;
	border: none;
	font-size: 17px;
	max-width: 120px;
}

.todo-item {
	padding: 12px 7px 12px 8px;
   text-align: left;
	margin-bottom:15px;
	color: #fff;
	border-radius: 10px;
	background-color: #c20505;
	font-size: 15px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.todo-item-remove{
	cursor: pointer;
}


